"use client";

import { useState, useRef } from 'react';
import { QRCodeSVG } from 'qrcode.react';
import * as htmlToImage from 'html-to-image';

export default function Home() {
  const [isGenerating, setIsGenerating] = useState(false);
  const cardRef = useRef<HTMLDivElement>(null);

  // vCard format for QR code
  const vCardData = `BEGIN:VCARD
VERSION:3.0
FN:S.Babu M.Com., FCA., DISA., LLB., ACMA., ACS.
ORG:S BABU AND COMPANY
TITLE:Proprietor
TEL:+91 9442627867
EMAIL:sbabuca@gmail.com
EMAIL:office@sbabuandcompany.com
ADR:;;37, First Floor, Afza Manzil, Perumal Kovil Street;Round Road Pudur;DINDIGUL;624005;India
URL:http://sbabuandcompany.com
END:VCARD`;

  const downloadAsImage = async () => {
    if (!cardRef.current) return;
    setIsGenerating(true);

    try {
      const image = await htmlToImage.toPng(cardRef.current);
      const link = document.createElement('a');
      link.href = image;
      link.download = 'visiting-card.png';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      setIsGenerating(false);
    } catch (error) {
      console.error('Error creating image:', error);
      setIsGenerating(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-100 to-gray-200 flex flex-col items-center justify-center p-4">
      {/* Visiting card size: 450px x 280px with glittering gold background */}
      <div 
        ref={cardRef}
        className="w-[450px] h-[280px] rounded-lg shadow-2xl p-6 flex flex-col"
        style={{ 
          background: 'linear-gradient(135deg, #FFD700 0%, #FFA500 25%, #FFD700 50%, #FFED4E 75%, #FFD700 100%)',
          backgroundSize: '200% 200%',
          animation: 'shimmer 3s ease-in-out infinite'
        }}
      >
        {/* Top half - Name and Company details with QR Code */}
        <div className="flex-1 flex">
          {/* Left side - Name and Company */}
          <div className="flex-1 flex flex-col justify-center pr-4">
            <div>
              <div className="flex items-baseline space-x-2">
                <h1 className="text-xs font-bold text-blue-700 leading-none">S.Babu</h1>
                <p className="text-xs text-blue-600 leading-none">M.Com., FCA., DISA., LLB., ACMA., ACS.</p>
              </div>
              <p className="text-xs font-medium text-blue-700 leading-none">Proprietor</p>
              <div>
                <h2 className="text-sm font-bold text-blue-800 leading-none">S BABU AND COMPANY</h2>
                <p className="text-xs text-blue-600 leading-none">Chartered Accountants</p>
              </div>
            </div>
          </div>
          
          {/* Right side - QR Code */}
          <div className="flex flex-col justify-start items-end">
            <div className="text-center">
              <QRCodeSVG 
                value={vCardData}
                size={80}
                level="H"
                includeMargin={false}
              />
              <p className="text-xs text-blue-500 mt-1">Scan to save</p>
            </div>
          </div>
        </div>
        
        {/* Center line separator */}
        <div className="border-t border-blue-300 my-3"></div>
        
        {/* Bottom half - Address and Contact details - Compact with no empty lines */}
        <div className="flex-1 flex flex-col justify-start space-y-2">
          {/* Address section */}
          <div className="flex justify-between">
            <div className="text-xs text-blue-600">
              <p className="font-semibold text-blue-800 leading-none">Office Address:</p>
              <p className="leading-tight">No.:37, First Floor, Afza Manzil, Perumal Kovil Street,</p>
              <p className="leading-tight">Round Road Pudur, DINDIGUL-624005, Tamilnadu, India</p>
            </div>
            <div className="text-xs text-blue-600 text-right">
              <p className="font-semibold text-blue-800 leading-none">Website:</p>
              <p className="leading-tight">http://sbabuandcompany.com</p>
            </div>
          </div>
          
          {/* Contact details - No margin top, directly below address */}
          <div className="flex justify-between items-start">
            <div className="text-xs text-blue-600">
              <span className="font-semibold text-blue-800">Mobile: </span>
              <span>+91 9442627867</span>
            </div>
            <div className="text-xs text-blue-600 text-right">
              <div><span className="font-semibold text-blue-800">Email: </span>sbabuca@gmail.com</div>
              <div>office@sbabuandcompany.com</div>
            </div>
          </div>
        </div>
      </div>

      <button
        onClick={downloadAsImage}
        disabled={isGenerating}
        className={`mt-6 ${
          isGenerating 
            ? 'bg-gray-400 cursor-not-allowed' 
            : 'bg-blue-600 hover:bg-blue-700'
        } text-white font-semibold py-2 px-4 rounded-lg shadow-md transition-colors duration-200`}
      >
        {isGenerating ? 'Generating Image...' : 'Download Card'}
      </button>

      <style jsx global>{`
        @keyframes shimmer {
          0% { background-position: 0% 50%; }
          50% { background-position: 100% 50%; }
          100% { background-position: 0% 50%; }
        }
      `}</style>
    </div>
  );
}
